
/* @group general */

body {
	margin: 0 auto;
	width: auto;
}

.container {
	width: auto;
	position: relative;
	border: none;
	-moz-box-shadow: none;
	-webkit-box-shadow: none;
	box-shadow: none;
	background-color: #f9f5f2;
}

.proportional {
	width: 100%;
	padding-bottom: 100%;
    position: relative;
}

.aspect1 {
	padding-bottom: 120%;
}

.aspect2 {
	padding-bottom: 60%;
}

.aspect3 {
	padding-bottom: 40%;
}

table {
	width: auto;
	margin: 0;
}

td+td {
	text-align: center;
	width: auto;
}

/* @end */

/* @group Error */
	
#error {
	height: 100%;
	background-image: none;
	padding: 20px;
}

#error_copy {
	font-size: 1.4em;
	margin-left: 0;
	padding-top: 0;
}

#error_copy h2, #error_copy p {
	color: #777;
}

/* @end */

/* @group Header */

header {
	background: url('/images/main/pk_logo.png') 20px 10px no-repeat rgb(55, 34, 24);
	height: 70px;
	width: auto;
}
	
header h1, header h2 {
	font-size: 3px;
	text-indent: -9999px;
	margin: 0;
	padding: 0;
}

a.button {
	position: absolute;
	right: 20px;
	top: 23px;
	width: 30px;
	height: 25px;
	background: url('/images/main/menu_button.png') no-repeat left top;
	background-size: contain;
	display: block;
	margin: 0;
}

nav {
	height: 30px;
	float: none;
	margin: 0;
}

nav ul {
	padding-top: 10px;
	padding-left: 10px;
	width: 140px;
	background-color: rgb(55, 34, 24);
	line-height: 5px;
	display: none;
	position: absolute;
	z-index: 10;
	top: 70px;
	right: 0;
}

nav li {
	list-style-type: none;
	display: block;
	font-size: 1.3em;
	margin-bottom: 40px;
}

nav li:last-of-type {
	margin-bottom: 30px;
}

nav li a {
	color: #F9E0A4;
	text-decoration: none;
	margin: 10px 10px;
	opacity: .7;
	-webkit-transition: none;
	-moz-transition: none;
	transition: none;
}

nav li a:hover {
	color: rgb(255,245,220);
	opacity: 1;
}

li.active a {
	opacity: 1;
}

/* @end */

/* @group Admin */


#admin_copy {
	margin: 0;
}

#admin_copy ul li {
	display: block;
	float: none;
	text-decoration: none;
	margin: 0;
	background-color: rgb(55, 34, 24);
	padding: 10px;
}

/* @end */

/* @group Home */

#home {
	width: auto;
	height: 3000px;
	background: none;
	float: none;
	padding: 0;
}

#home_image {
	background: url('/images/home/katharina.jpg') 0px 0px no-repeat;
	background-size: contain;
	float: none;
	margin: 0;
	width: auto;
	height: auto;
}

#home .proportional > #home_image {
	position: absolute;
    top: 0; bottom: 0; left: 0; right: 0;
}

#homecopy {
	display: none;
}

#homecopy img {
	margin: 0;
}
	
#reputation {
	text-align: center;
	font: 1em/1.3em "Helvetica Neue", Arial, Helvetica, Geneva, sans-serif;
	margin-left: -30px;
	color: #e0ddd8;
}

#home_news {
	position: relative;
	width: auto;
	margin-top: 0;
}

.home_news {
	position: relative;
	width: 250px;
	margin: auto;
	height: 480px;
	float: none;
	background-color: rgb(55,34,24);
	-moz-box-shadow: none;
	-webkit-box-shadow: none;
	box-shadow: none;
	margin-bottom: 20px;
}

.home_news:hover {
	margin-top: 0;
	-moz-box-shadow: none;
	-webkit-box-shadow: none;
	box-shadow: none;
}

.home_news_pic {
	width: 250px;
	height: 300px;
	float: none;
}

.home_news_copy {
	width: 230px;
	padding: 10px;
	float: none;
	clear: none;
	background-color: none !important;
}

.home_news_copy p,
.home_news_copy h3 {
	color: rgb(255,247,240);
	background-color: none !important;
	margin: 10px;
}

.home_news_copy h3 {
	font-size: 1em;
	line-height: 1.2em;
}

.home_news_copy p {
	font-size: .8em;
	font-weight: normal;
}

#emily h3, #emily p {
	text-indent: -9999px;
	
}

.newspara {
	font-family: Arial, Helvetica, sans-serif;
	font-size: 0.55em;
	font-weight: normal;
	text-align: left;
}

.newscenter {
	top: 0;
	left: 0;
	position: relative;
	text-align: center;
	font-size: .75em;
	font-weight: bold;
	margin: 0;
	padding: 10px;
	padding-bottom: 20px;
}

.paraend {
	font-size: 1em;
	font-weight: bold;
	font-family: Georgia, "Times New Roman", Times, serif;
}

.offerHeading {
	font-size: 1.2em;
	font-weight: bold;
}

.offer {
	font-size: 1em;
	font-weight: bold;
}

.offerSmall {
	font-size: .6em;
	
}

#feedback_feed {
	width: auto;
	height: 140px;
	text-align: left;
	position: relative;
	font-size: .8em;
	top: 0;
	left: 0;
	display: none;
	padding: 30px;
}

#feedback_feed ul {
	padding: 0;
}

#feedback_feed ul li {
	color: rgb(55, 34, 24);
	list-style-type: none;
	font-family: Georgia, "Times New Roman", Times, serif;
	font-style: italic;
}

#feedback_feed ul li .client {
	font: .7em "Helvetica Neue", Arial, Helvetica, Geneva, sans-serif;
}

#feedback_feed a {
	color: rgb(55, 34, 24);
	display: block;
	padding-top: 20px;
	font-size: 1.4em;
}

/* @end */

/* @group Salon */

#salon {
	width: auto;
	height: 100%;
	background-image: none;
	background-repeat: no-repeat;
}

#saloninfo {
	position: relative;
	top: 0;
	width: auto;
	padding: 20px;
	
}

#saloninfo a {
	color: rgb(55,34,24);
}


#saloninfo a:hover {
	border-bottom: dashed 1px #666;
}


#salonimages {
	position: relative;
	margin: 0;
	top: 0;
	left: 0;
	width: auto;
	height: 100%;
	background-color: #281d02;
	padding: 10px 0;
	padding-bottom: 30px;
	margin-bottom: 20px;
	border: 2px solid #FFF;
}

#salonimages img {
	float: none;
	margin: 20px;
	margin-bottom: 10px;
	margin-left: 30px;
	border: 1px solid #FFF;
}

/* @end */

