/* @group News */

#newspage {
	width: auto;
	margin: auto;
	height: 100%;
	padding: 20px;
}

p.newslink {
	margin: 0;
}

#accordion {
	width: auto;
}

#accordion article.article {
	margin: 20px 0 ;
	border-top: 1.5px solid rgb(55, 34, 24);
	padding-top: 30px;
	position: relative;
}


#accordion article.article:first-of-type {
	border: none;
}

.visible {
	min-height: 200px;
}



.hidden {
	margin-bottom: 20px;

}

#accordion article.article section.hidden img.image_list {
	margin-bottom: 15px;
	float: none;
}

a.anchor {
	position: absolute;
	top: -100px;
}

.author {
	font-family: 'PT Sans', sans-serif;
	font-size: .6em;
	opacity: .8;
	position: relative;
	float: right;
	left: 0;
	line-height: 2em;

}


#accordion article.article section.visible p,
#accordion article.article section.visible h3,
#accordion article.article section.visible h4 {
	margin-left: 0;
}

#accordion article.article section.hidden p,
#accordion article.article section.hidden h3,
#accordion article.article section.hidden h4,
#accordion article.article section.hidden h5,
#accordion article.article section.hidden iframe {
	margin-left: 0;
}

#accordion article.article section.visible h3 {
	font-size: 1.2em;
	line-height: 1.2em;
}

#accordion article.article section.hidden h4 {
	font-size: 1.3em;
}

#accordion article.article section.hidden h4 {
	font-size: 1.1em;
}

#accordion article time {
	margin-left: 0;
	font-family: 'PT Sans', sans-serif;
	font-size: .6em;
	opacity: .8;
	text-align: right;
}

#accordion article.article section.hidden iframe {
	margin: 10px 10px 20px 0;
	border: 1px solid #333;
	padding: 5px;
	float: none;
}


#accordion article.article section.hidden img {
	float: none;
}

#accordion article.article section.hidden dl dt {
	color: rgb(55,34,24);
	line-height: 1.4em;
	margin-bottom: 5px;
	font-weight: bold;
	margin-left: 0;
}

#accordion article.article section.hidden dl dd {
	color: rgb(55,34,24);
	margin-left: 0;
	margin-bottom: 8px;
	line-height: 1.4em;
	margin-left: 0;
}

#accordion article.article section.hidden ul.list li {
	display: block;
	font-size: .9em;
	margin: .3em 0;
}

#accordion article.article section.hidden ul.tigilist li {
	display: block;
	margin-left: 0;
}

#accordion article.article section.hidden ul.prodlist li {
	display: block;
	margin-left: 0;
	font-family: "Helvetica Neue", Arial, Helvetica, Geneva, sans-serif;
	font-size: .9em;
	line-height: 1.5em;
}

#accordion article.article section.visible img {
	float: left;
	margin-right: 10px;
	margin-bottom: 10px;
}

#accordion article.article section.hidden img {
	float: none;
	margin-bottom: 30px;
}


p.reveal {
	font-size: 1em;
	display: block;
	opacity: .7;
}

#accordion article.article section p a {
	color: rgb(75,54,44);
}

#accordion article.article section p a:hover {
	border-bottom: 1px dotted rgb(55,34,24);
}

section#newspage a {
	color: rgb(55,34,24);
}

section#newspage a:hover {
	border-bottom: 1px dashed rgb(55,34,24);
}

section#newspage a p:hover {
	opacity: .6;
}

#accordion article.article section.hidden img.news-image {
	margin-left: 0;
	width: 100%;
}

